import React, { useEffect, useState } from "react";
import { MapContainer, TileLayer, Marker, Popup } from "react-leaflet";
import { Icon } from "leaflet";
import MarkerIcon from "../Icons/Marker.svg"

import "leaflet/dist/leaflet.css";
import './MyMapContainer.css';

import places from "../Data/allowedWordPlaces.json";
import OverlayCard from "../Components/OverlayCard/OverlayCard";
import CopyRight from "../Components/Copyright/Copyright";

const MyMapContainer = () => {
  const [searchField, setSearchField] = useState("");
  const [filteredPlaces, setFilteredPlaces] = useState([]);

  useEffect(() => {
    if (searchField.length < 3) {  
      setFilteredPlaces([]);
      return;
    }

    setFilteredPlaces(
      places.filter((place) => {
        return place.properties.spelling
          .toLowerCase()
          .includes(searchField.toLowerCase());
      })
    );
  }, [searchField]);

  return (
    <div className="relative flex h-screen">
      <OverlayCard setSearchField={setSearchField} />
      <CopyRight/>
      <div className="z-0 flex grow ">
        <MapContainer
          center={[64.490247, 25.716601]}
          zoom={5}
          minZoom={5}
          scrollWheelZoom={true}
          zoomControl={false}
        >
          <TileLayer
            attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
            url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          />
          {searchField !== "" &&
            filteredPlaces.map((filteredPlace) => (
              <Marker
                key={filteredPlace.id}
                position={filteredPlace.geometry.coordinates}
                icon={ new Icon({ iconUrl: MarkerIcon, iconSize: [15, 15] })}
              >
                <Popup closeButton={false}>
                <div>
                {filteredPlace.properties.spelling}
                </div>
                </Popup>
              </Marker>
            ))}
        </MapContainer>
      </div>
    </div>
  );
};

export default MyMapContainer;
