import React from 'react';
import { DebounceInput  } from 'react-debounce-input';
import './CardBottom.css';

const CardBottom = ({handleChange}) => {
    return (
      <div class="relative">
        <div class="flex absolute inset-y-0 left-0 items-center pl-3 pointer-events-none">
          <svg aria-hidden="true" className="w-5 h-5 text-gray-500 dark:text-gray-400" fill="none" stroke="currentColor" viewBox="0 0 24 24" xmlns="http://www.w3.org/2000/svg"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M21 21l-6-6m2-5a7 7 0 11-14 0 7 7 0 0114 0z"></path></svg>
        </div>
        <DebounceInput
          className="card-search block p-4 pl-10 w-full text-sm rounded-lg border border-gray-300 focus:ring-blue-500 focus:border-blue-500"
          placeholder="Kirjoita vaikka kusi..."
          minLength={3}
          debounceTimeout={350}
          onChange={handleChange}
/>
      </div>
    )
}

export default CardBottom;